import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { checkMembership } from '../services/api';

export default function MemberCheck() {
  const [nom, setNom] = useState('');
  const [prenom, setPrenom] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const navigate = useNavigate();

const handleVerification = async (e) => {
  e.preventDefault();
  setLoading(true);
  setError('');
  try {
    const result = await checkMembership(nom, prenom);
    console.log("Résultat vérification:", result); // Log pour debug
    
    if (result.isPaid) {
      // Forcer la navigation avec un délai court pour assurer le traitement
      setTimeout(() => {
        navigate('/confirmation');
      }, 100);
    } else {
      setError(result.message);
    }
  } catch (err) {
    setError(err.message || "Erreur lors de la vérification");
  } finally {
    setLoading(false);
  }
};

  return (
    <div className="member-check">
      <h2>Vérification d'adhésion</h2>
      <form onSubmit={handleVerification}>
        <input
          type="text"
          placeholder="Nom"
          value={nom}
          onChange={(e) => setNom(e.target.value)}
          disabled={loading}
        />
        <input
          type="text"
          placeholder="Prénom"
          value={prenom}
          onChange={(e) => setPrenom(e.target.value)}
          disabled={loading}
        />
        
        <button 
          type="submit" 
          disabled={loading}
          className={loading ? 'loading' : ''}
        >
          {loading ? 'Vérification en cours...' : 'Vérifier'}
        </button>

        {error && <div className="error-message">{error}</div>}
      </form>

      <button 
        onClick={() => navigate('/')}
        className="back-button"
      >
        Retour à l'accueil
      </button>
    </div>
  );
}
